import { mapState, mapMutations } from 'vuex'
export default {
	data() {
		return {
			userPhone: '18884027431',
			checkCode: '723186',
			disabled: false,
			content: '获取验证码',
			totalTime: 0,
		}
	},
	computed: {
		...mapState(['hasLogin']),  //对全局变量hasLogin进行监控
	},
	onLoad() {
	},
	methods: {
		...mapMutations(['login']),  //对全局方法login进行监控
		onChangePhone(v) {
			this.userPhone = v.detail
		},
		onChange(v) {
			this.checkCode = v.detail
		},
		getCode() {
			let ruleValue = this.$utils.checkPhone(this.userPhone)
			if(ruleValue) {
				this.$http.get({
					url: '/v1/rest/public/authCode',
					data:{
						phone: this.userPhone,
					}
				}).then(resp => {
					console.log(resp)
				})
				this.totalTime = 60
				this.timer = setInterval(() => {
					this.totalTime--
					this.content = this.totalTime + '秒后重试'
					this.disabled = true
					if(this.totalTime <= 0){
						this.content = '获取验证码'
						this.disabled = false
						clearInterval(this.timer)
					}
				},1000)
			}else{
				uni.showToast({
					title: '请输入有效的手机号码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		// 短信验证码登录
		handleLogin() {
			let ruleValue = this.$utils.checkPhone(this.userPhone)
			if(!ruleValue){
				uni.showToast({
					title: '请输入有效的手机号码！',
					duration: 2000,
					icon: 'none'
				});
			}else if(this.checkCode == ''){
				uni.showToast({
					title: '请输入验证码！',
					duration: 2000,
					icon: 'none'
				});
			}else{
				this.$http.post({
					url: '/v1/rest/login/authCodeLogin',
					data:{
						phone: this.userPhone,
						authCode: this.checkCode
					}
				}).then(resp => {
					console.log(resp)
					if(resp.status == 200) {
						const userInfo = {
							userPhone: resp.data.phone,
							userId: resp.data.userId,
							token: resp.data.token
						}
						this.login(userInfo)
						uni.setStorage({            //把用户信息保存到本地缓存
							key: 'userInfo',
							data: userInfo
						})
						if(resp.data.initCreate) {
							uni.redirectTo({
								url: '/pages/userInfo/registerInfo/registerInfo'
							})
						}else{
							uni.navigateBack() 
						}
					}
				})
			}
		},
		// 微信登录
		getuserinfo(e){
			const that = this
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
					console.log(loginRes)
			    that.$http.get({
			    	url: '/v1/rest/login/getOpenId',
			    	data:{
			    		code: loginRes.code,
			    	}
			    }).then(res => {
			    	if(res.status == 200) {
							console.log(res)
							if(res.data.initOpenId) {  //第一次微信快捷登录需绑定手机号
								uni.setStorage({
									key: 'openId',
									data: res.data.openId
								})
								uni.getUserInfo({
									provider: 'weixin',
									lang: 'zh_CN',
									success: function (resp) {
										console.log(resp)
										uni.setStorage({            //把用户信息保存到本地缓存
											key: 'weixinUserInfo',
											data: resp.userInfo
										})
										uni.redirectTo({
											url: '/pages/bindPhone/bindPhone'
										})
									}
								})
							}else{  //非第一次微信快捷登录，不需绑定
								const userInfo = {
									userPhone: res.data.phone,
									userId: res.data.userId,
									token: res.data.token
								}
								that.login(userInfo)
								setTimeout(function () {
									uni.navigateBack() 
								},200)
							}
						}
			    })
			  }
			});
		}
	}
}
